import { h, Component } from 'preact';
import { useEffect } from 'preact/hooks';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import linkstate from 'linkstate';
import Markdown from 'preact-markdown';
import { toJS } from 'mobx';

import Header from './header';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

const VACCINE_TYPES = mu.getVaccineTypesVet();

const apiServer = util.getApiServer();

function PageTitle(title = '') {
  useEffect(() => {
    document.title = `The Musher: ${title}`;
  }, [title]);
  return <h1>{title}</h1>;
}

const initialState = {
	isLoading: false,
	email: '',
	submenu: 'ads',
	jwtToken: util.getJwtToken(),
};

@observer
class Teams extends Component {
    selectTeam = async (e) => {
        e.preventDefault();
        const id = e.target.value;
        this.setState({ displayTeam: parseInt(id, 10) });
    }

    render() {
        const { teams = [], displayUser } = this.props;
        const { userStore } = this.props.stores;
        const { isLoading } = userStore;
        const defaultTeam = teams[0] ? teams[0] : {};
        const { displayTeam = defaultTeam.id } = this.state;
        return (
            <div class='bg-light p-4'>
                {isLoading.getDogs ? <>
                    Loading...
                </> : <>
                    <label for='team'><i class='fas fa-users' /> <Text id='dog.veterinary.team'>Team</Text></label>
                    <select
                        class='form-control form-control-lg mb-2'
                        id='team'
                        onChange={this.selectTeam}
                    >
                        <option value=''>-- <Text id='dog.veterinary.choose-team'>Choose team</Text> --</option>
                        {teams && teams.map(team => {
                            return (<>
                                <option value={team.id}>
                                    {team.name}
                                </option>
                            </>);
                        })}
                    </select>

                    {teams && teams.map(team => <>
                        {displayTeam === team.id && <>
                            <Dogs stores={this.props.stores} dogs={team.dogs} displayUser={displayUser} />
                        </>}
                    </>)}                
                </>}
            </div>
        );
    }
}

@observer
class Dogs extends Component {
    constructor(props) {
        super(props);
        this.state = {
            missingChipId: {},
            selectedDogs: [],
            selectedVaccines: [],
        };
    }

    selectDog = (e) => {
        const { id } = e.target.closest('tr').dataset;
        const { selectedDogs, missingChipId } = this.state;
        const dogId = parseInt(id, 10);
        const idx = selectedDogs.indexOf(dogId);

        const { dogs } = this.props;
        const dog = dogs.find(e => e.id === dogId);
        // console.log({ dog });
        if (dog.chipId.length < 10) {
            missingChipId[dogId] = !missingChipId[dogId];
            this.setState({ missingChipId });
            return '';
        }

        if (idx === -1) {
            selectedDogs.push(dogId);
        } else {
            selectedDogs.splice(idx, 1);
        }
        this.setState({ selectedDogs });
    }

    selectDogSection = (e) => {
        const { id } = e.target.closest('i').dataset;
        const { selectedDogs, missingChipId } = this.state;
        const dogId = parseInt(id, 10);
        const idx = selectedDogs.indexOf(dogId);

        const { dogs } = this.props;
        const dog = dogs.find(e => e.id === dogId);
        if (dog.chipId.length < 10) {
            missingChipId[dogId] = !missingChipId[dogId];
            this.setState({ missingChipId });
            return '';
        }

        if (idx === -1) {
            selectedDogs.push(dogId);
        } else {
            selectedDogs.splice(idx, 1);
        }
        this.setState({ selectedDogs });
    }

    selectAllDogs = () => {
        const { dogs } = this.props;
        const selectedDogs = dogs.filter(e => e.chipId.length > 10).map(e => e.id);
        const dogMissingChipId = dogs.filter(e => e.chipId.length < 10).map(e => e.id);

        const { missingChipId } = this.state;
        for (let i = 0, l = dogMissingChipId.length; i < l; i += 1) {
            const dogId = dogMissingChipId[i];
            missingChipId[dogId] = !missingChipId[dogId];
        }

        this.setState({ selectedDogs, missingChipId });
    }

    removeAllDogs = () => {
        const selectedDogs = [];
        this.setState({ selectedDogs });
    }

    selectVaccine = (e) => {
        const { id } = e.target.closest('tr').dataset;
        const { selectedVaccines } = this.state;
        const vaccineId = parseInt(id, 10);
        const idx = selectedVaccines.indexOf(vaccineId);
        if (idx === -1) {
            selectedVaccines.push(vaccineId);
        } else {
            selectedVaccines.splice(idx, 1);
        }
        this.setState({ selectedVaccines });
    }

    addVaccinesToDogs = async () => {
        const { selectedDogs, selectedVaccines } = this.state;
        const { userStore, vaccineLogStore } = this.props.stores;
        const { user } = userStore;
        const data = [];
        const { dogs } = this.props;
        // console.log({ selectedDogs, selectedVaccines });
        const vetData = {
            id: user.id,
            firstname: user.firstname,
            lastname: user.lastname,
            email: user.email,
            image: user.image,
        };

        for (let i = 0, l = selectedDogs.length; i < l; i += 1) {
            const dogId = selectedDogs[i];
            const dog = dogs.find(e => e.id === dogId);
            const dogData = {
                id: dog.id,
                user: dog.user,
                team: dog.team,
                name: dog.name,
                shortname: dog.shortname,
                chipId: dog.chipId,
            };

            for (let j = 0, m = selectedVaccines.length; j < m; j += 1) {
                const vaccineId = selectedVaccines[j];
                const vaccine = VACCINE_TYPES.find(e => e.id === vaccineId);
                const vaccineData = {
                    id: vaccine.id,
                    name: vaccine.name,
                    agens: vaccine.agens,
                    comment: vaccine.comment,
                };
                data.push({
                    dog: { ...dogData },
                    vaccine: { ...vaccineData },
                    veterinary: { ...vetData },
                });
            }
        }
        await vaccineLogStore.insert({ data });
        this.setState({
            data,
            selectedDogs: [],
            selectedVaccines: [],
        });
        userStore.addVaccineLogStatuses(data);
    }

    toggleShowAddVaccineToDogs = (e) => {
        const { showAddVaccineToDogs = false } = this.state;
        this.setState({
            showAddVaccineToDogs: !showAddVaccineToDogs,
        });
    }

    render() {
        const { selectedDogs, selectedVaccines, missingChipId, data, showAddVaccineToDogs = false } = this.state;
        const { userStore } = this.props.stores;
        const { isAdmin, isVeterinary, vaccineStatuses, vaccineLogStatuses, isLoading } = userStore;
        const { dogs } = this.props;

        const selectedAgens = VACCINE_TYPES
            .filter(e => selectedVaccines.indexOf(e.id) > -1)
            .map(vac => vac.agens.map(e => e.name))
            .flat();

        return (<>
            <div class='table-responsive mt-3'>
                <button
                    class='btn btn-lg btn-block btn-outline-success mt-5'
                    onClick={this.selectAllDogs}
                >
                    <Text id='dog.veterinary.select-all-dogs'>Select all dogs</Text>
                </button>
                {selectedDogs && selectedDogs.length > 0 && <>
                    <button
                        class='btn btn-sm btn-block btn-link'
                        onClick={this.removeAllDogs}
                    >
                        <Text id='dog.veterinary.remove-all-dogs'>Remove all dogs</Text>
                    </button>
                </>}

                <table class='table table-striped'>
                    <thead>
                        <tr>
                            <th scope='col' style='width: 100px;'>&nbsp;</th>
                            <th scope='col'><Text id='dog.veterinary.name'>Name</Text></th>
                        </tr>
                    </thead>
                    <tbody>
                        {dogs && dogs.map(dog => {
                            let image;
                            if (dog.images && dog.images[0]) {
                                image = dog.images[0];
                            }
                            if (dog.image) {
                                image = dog.image;
                            }
                            const isSelected = selectedDogs.indexOf(dog.id) > -1;
                            const isMissingChipId = missingChipId[dog.id];
                        
                            return (<>
                                <tr class='pointerCursor' onClick={this.selectDog} data-id={dog.id}>
                                    <td class='position-relative'>
                                        <div
                                            class={`text-center border rounded-circle imageRounded text-muted pt-2`}
                                            style={`${image ? `background-image: url('${image.s3ThumbLink}');` : ''}`}
                                        >
                                            {!image && <span class='font-weight-lighter' style='font-size: 35px;'>
                                                {util.ucfirst(dog.name, true)}
                                            </span>}
                                        </div>
                                        {isSelected && <span class='position-absolute display-4' style='top: 0px; left: 0px;'>
                                            <i class='fas fa-check text-success' />
                                        </span>}
                                        {isMissingChipId && <span class='position-absolute display-4' style='top: 0px; left: 0px;'>
                                            <i class='fas fa-hand-paper text-danger' />
                                        </span>}
                                    </td>
                                    <td>
                                        <span class='float-right'>
                                            {dog.birth && <span class='mr-2'>{util.age(dog.birth, dog.deceased)}</span>}
                                            {dog.gender === 'female' ? <i class='fas fa-venus' /> : <i class='fas fa-mars' />}
                                        </span>
                                        <h5>{dog.name} {dog.shortname && <span class='font-weight-lighter'> - '{dog.shortname}'</span>}</h5>
                                        <div>
                                            <span class='text-muted'>
                                                {dog.chipId && <><i class='fas fa-microchip' /> {dog.chipId}</>}
                                            </span>
                                        </div>
                                        <div>
                                            {vaccineLogStatuses && vaccineLogStatuses.filter(e => e.chipId === dog.chipId).map((vac) => {
                                                return (
                                                    <span class={`badge badge-${vac.inKarens ? 'warning' : 'success'} mr-2 mb-1`}>
                                                        {vac.vaccineAgens.name}
                                                        <span class='font-weight-lighter ml-2'>
                                                            <i class='fas fa-calendar-check' /> {util.isoDate(vac.vaccineAgens.durationEnd, false, false, true)}
                                                            {vac.inKarens && <>
                                                                <br />
                                                                (karens: {util.format(vac.karensDaysLeft, 0)} days left)
                                                            </>}
                                                        </span>
                                                    </span>
                                                );
                                            })}
                                        </div>
                                    </td>
                                </tr>
                            </>);
                        })}
                    </tbody>
                </table>
            </div>

            <div class='mt-5'>
                {selectedDogs && selectedDogs.length > 0 && <>
                    <div class='form-row align-items-center'>
                        <div class='col-12'>
                            <label for='vaccineTypeInput'><i class='fas fa-syringe' /> <Text id='dog.addlog.vaccine-type'>Vaksinetype</Text></label>
                            <table class='table table-striped'>
                                <thead>
                                    <tr>
                                        <th scope='col' style='width: 100px;'>&nbsp;</th>
                                        <th scope='col'><Text id='dog.veterinary.name'>Name</Text></th>
                                        <th scope='col' style='width: 130px;'><Text id='dog.veterinary.image'>Image</Text></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {VACCINE_TYPES && VACCINE_TYPES.map(vac => {
                                        const isChecked = selectedVaccines.indexOf(vac.id) > -1;
                                        return (
                                            <tr
                                                data-id={vac.id}
                                                onClick={this.selectVaccine}                                    
                                            >
                                                <td>
                                                    {isChecked ? <>
                                                        <div class='display-4 text-center'>
                                                            <i class='fas fa-check text-success' />
                                                        </div>
                                                    </> : <>
                                                        <div class='display-4 border rounded-lg text-center'>
                                                            <i class='fas fa-radio text-success' />
                                                        </div>
                                                    </>}
                                                </td>
                                                <td>
                                                    {vac.name}<br />
                                                    <small>
                                                        {vac.agens.map(agens => <>
                                                            <span class='badge badge-dark mr-2 mt-1'>
                                                                {agens.name} ({agens.durationMonths} months, {agens.karensDays} days)
                                                            </span>
                                                        </>)}
                                                    </small>
                                                </td>
                                                <td>
                                                    {vac.image && <img class='img-fluid' src={`${apiServer}${vac.image}`} />}
                                                </td>
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </table>
                        </div>
                        <div class='col-12 pt-4'>
                            <label for='team'><i class='fas fa-dog' /> <Text id='dog.veterinary.choosen-dogs'>Valgte hunder</Text></label>
                            <div class='d-flex flex-wrap'>
                                {dogs && dogs.map(dog => {
                                    let image;
                                    if (dog.images && dog.images[0]) {
                                        image = dog.images[0];
                                    }
                                    if (dog.image) {
                                        image = dog.image;
                                    }
                                    const isSelected = selectedDogs.indexOf(dog.id) > -1;
                                    if (!isSelected) {
                                        return '';
                                    }
                                    const dogHasAgens = vaccineLogStatuses.filter(v => v.chipId === dog.chipId).map(vac => vac.vaccineAgens.name).flat();

                                    return (<>
                                        <section class='p-2 position-relative' style='width: 120px;'>
                                            <div
                                                class={`text-center border rounded-circle imageRounded text-muted pt-2`}
                                                style={`${image ? `background-image: url('${image.s3ThumbLink}');` : ''}`}
                                            >
                                                {!image && <span class='font-weight-lighter' style='font-size: 35px;'>
                                                    {util.ucfirst(dog.name, true)}
                                                </span>}
                                            </div>
                                            <small>
                                                {dog.name}
                                            </small><br />
                                            <small class='text-muted'>
                                                {dog.birth && <span class='mr-2'>{util.age(dog.birth, dog.deceased)}</span>}
                                                {dog.gender === 'female' ? <i class='fas fa-venus' /> : <i class='fas fa-mars' />}
                                            </small>

                                            {selectedAgens && <>
                                                {selectedAgens.filter(e => dogHasAgens.indexOf(e) > -1).map(agen => <>
                                                    <span class={`badge badge-danger mr-2 mb-1`}>
                                                        <i class='fas fa-exclamation-triangle' /> {agen}
                                                    </span>
                                                </>)}
                                            </>}

                                            <div class='position-absolute' style='top: 0px; right: 0px; font-size: 2.0em;'>
                                                <i class='fas fa-times-circle text-danger' onClick={this.selectDogSection} data-id={dog.id} />
                                            </div>
                                        </section>
                                    </>);
                                })}
                            </div>
                        </div>
                        <div class='col-12 pt-4'>
                            <label><i class='fas fa-syringe' /> <Text id='dog.veterinary.chosen-vaccines'>Valgte vaksiner</Text></label>
                            {VACCINE_TYPES && VACCINE_TYPES.map(vac => {
                                const isChecked = selectedVaccines.indexOf(vac.id) > -1;
                                if (!isChecked) {
                                    return '';
                                }
                                return (<div class='mb-3'>
                                    <span class='font-weight-lighter'>+</span> {vac.name}<br />
                                    <small>
                                        {vac.agens.map(agens => <>
                                            <span class='badge badge-dark mr-2 mt-1'>
                                                {agens.name} ({agens.durationMonths} months, {agens.karensDays} days)
                                            </span>
                                        </>)}
                                    </small>
                                </div>);
                            })}
                        </div>

                        {selectedVaccines && selectedVaccines.length > 0 ? <>
                            {showAddVaccineToDogs ? <>
                                <div class='col-12 pt-4'>
                                    <h5><Text id='dog.veterinary.are-you-sure' fields={{ dogs: selectedDogs.length }}>Are you sure you want to add vaccine to {selectedDogs.length} dog?</Text></h5>
                                    <button
                                        type='button'
                                        class='btn btn-lg btn-block btn-success mb-2'
                                        onClick={this.addVaccinesToDogs}
                                    ><Text id='dog.veterinary.yes-iam-sure'>Yes, I'm sure!</Text></button>
                                    <button
                                        type='button'
                                        class='btn btn-lg btn-block btn-danger mb-2'
                                        onClick={this.toggleShowAddVaccineToDogs}
                                    ><Text id='dog.veterinary.cancel'>Cancel</Text></button>
                                </div>                                                
                            </> : <>
                                <div class='col-12 pt-4'>
                                    <button
                                        type='button'
                                        class='btn btn-lg btn-block btn-success mb-2'
                                        onClick={this.toggleShowAddVaccineToDogs}
                                    ><Text id='dog.veterinary.add-vaccine-to-dogs' fields={{ dogs: selectedDogs.length }}>Add vaccine to {selectedDogs.length} dogs</Text></button>
                                </div>                        
                            </>}                        
                        </> : <>
                            <h3 class='text-muted mt-4'>
                                <Text id='dog.veterinary.choose-vaccines'>Velg vaksiner i listen over</Text>
                            </h3>
                        </>}

                    </div>

                </>}
            </div>

            {data && <>
                <div class='mt-5'>
                    <div class='row'>
                        <div class='col-12 pt-4'>
                            <h5><Text id='dog.veterinary.recepipt'>Receipt</Text></h5>
                            <table class='table table-striped'>
                                <thead>
                                    <tr>
                                        <th scope='col'><Text id='dog.veterinary.dog'>Dog</Text></th>
                                        <th scope='col'><Text id='dog.veterinary.vaccine'>Vaccine</Text></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {data && data.map(line => {
                                        const dog = line.dog;
                                        const vac = line.vaccine;
                                        return (<>                                
                                            <tr>
                                                <td>
                                                    {dog.name}<br />
                                                    <small class='text-muted'>
                                                        {dog.chipId}
                                                    </small>
                                                </td>
                                                <td>
                                                    {vac.name}
                                                    <small>
                                                        {vac.agens.map(agens => <>
                                                            <span class='badge badge-dark mr-2 mt-1'>
                                                                {agens.name} ({agens.durationMonths} months, {agens.karensDays} days)
                                                            </span>
                                                        </>)}
                                                    </small>
                                                </td>
                                            </tr>
                                        </>);
                                    })}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </>}

        </>);
    }
}


@observer
class Veterinary extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

    searchForMusher = async (e) => {
        e.preventDefault();
        const { userStore } = this.props.stores;
        const { search } = this.state;
        // console.log('search for musher:', search);
        this.setState({ isSearcing: true, displayUser: null });
        await userStore.searchUsers({ search });
        this.setState({ isSearcing: false });
    }

    selectMusher = async (e) => {
        const { id } = e.target.closest('tr').dataset;
        const { displayUser } = this.state;
        const userId = parseInt(id, 10);
        if (displayUser === userId) {
            this.setState({ displayUser: null });
        } else {
            this.setState({ displayUser: userId });
            const { userStore } = this.props.stores;
            await userStore.getDogs({ id: userId });
        }
    }

	componentDidMount() {
	}

	componentWillReceiveProps(nextProps) {
	}

	render() {
        const { displayUser, isSearcing, isLoading } = this.state;
		const { appState, userStore } = this.props.stores;
		const { isAdmin, isVeterinary, searchUsersResult, searchUserTeams } = userStore;
		const { language = 'no' } = appState;
        const { col = 12, offset = 0 } = this.props;
        const filterBy = (e) => {
            if (displayUser) {
                return e.id === displayUser;
            }
            return true;
        }

        if (!isVeterinary) {
            return (<>
                <div class='container-fluid h-100'>
                    <div class='row h-100'>
                        <div class='col-12 text-center my-auto'>
                            <div class='display-1 text-danger'>
                                <i class='fas fa-hand-paper' />
                            </div>
                            <h5><Text id='dog.veterinary.no-access'>No access!</Text></h5>
                            <p><Text id='dog.veterinary.attempts-logged'>All attempts are logged and reported.</Text></p>
                            <a class='mt-3 btn btn-lg btn-primary' href='/'><Text id='dog.veterinary.goto-frontpage'>Go to the frontpage</Text></a>
                        </div>
                    </div>
                </div>
            </>)
        }

		return (
			<>
				{/* <Header stores={this.props.stores} /> */}

				<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                    <div class='row'>
						<div class={`col-${col} offset-${offset} col-sm-8 col-lg-8 offset-sm-2 offset-lg-2`}>
                            <h1><Text id='dog.veterinary.title'>Veterinary search</Text></h1>

                            <form onSubmit={this.searchForMusher}>
                                <div class='form-row align-items-center'>
                                    <div class='col-10'>
                                        <label class='sr-only' for='vetSearch'><Text id='dog.veterinary.name'>Name</Text></label>
                                        <Localizer>
                                            <input
                                                type='text'
                                                class='form-control form-control-lg mb-2'
                                                id='vetSearch'
                                                placeholder={<Text id='dog.veterinary.search-placeholder'>Search for musher by email, cellphone or name...</Text>}
                                                onInput={linkstate(this, 'search')}

                                            />
                                        </Localizer>
                                    </div>
                                    <div class='col-2'>
                                        <button
                                            type='button'
                                            class='btn btn-lg btn-primary mb-2'
                                            onClick={this.searchForMusher}
                                        ><Text id='dog.veterinary.search'>Search</Text></button>
                                    </div>
                                </div>
                            </form>

                        </div>
						<div class={`col-${col} offset-${offset} col-sm-8 col-lg-8 offset-sm-2 offset-lg-2`}>
                            {isSearcing && <>
                                <div class='spinner-border text-primary' role='status'>
                                    <span class='sr-only'>
                                        <Text id='dog.veterinary.searching'>Searcing...</Text>
                                    </span>
                                </div>
                            </>}
                            {(!searchUsersResult || searchUsersResult.length === 0) ? <>
                                &nbsp;
                            </> : <div class='table-responsive'>
                                <table class='table table-striped'>
                                    <thead>
                                        <tr>
                                            <th scope='col' style='width: 100px;'>&nbsp;</th>
                                            <th scope='col'><Text id='dog.veterinary.name'>Name</Text></th>
                                            <th scope='col'><Text id='dog.veterinary.cellphone'>Cellphone</Text></th>
                                            <th scope='col'><Text id='dog.veterinary.email'>Email</Text></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {searchUsersResult && searchUsersResult.filter(filterBy).map(user => {
                                            let image;
                                            if (user.images && user.images[0]) {
                                                image = user.images[0];
                                            }
                                            if (user.image) {
                                                image = user.image;
                                            }
                                        
                                            return (<>
                                                <tr class='pointerCursor' onClick={this.selectMusher} data-id={user.id}>
                                                    <td>
                                                        <div
                                                            class={`text-center border rounded-circle imageRounded text-muted pt-2`}
                                                            style={`${image ? `background-image: url('${image.s3ThumbLink}');` : ''}`}
                                                        >
                                                            {!image && <span class='font-weight-lighter' style='font-size: 35px;'>
                                                                {user.firstname ? <>
                                                                    {util.ucfirst(user.firstname, true)}
                                                                    {util.ucfirst(user.lastname, true)}
                                                                </> : <>
                                                                    {util.ucfirst(user.email, true)}
                                                                </>}
                                                            </span>}
                                                        </div>                                                
                                                    </td>
                                                    <td>
                                                        {user.firstname} {user.lastname}<br />
                                                        {user.facebook && <a class='mr-2' href={mu.asFacebookLink(user.facebook)} target='_blank' rel='noreferrer'>
                                                            <i class='fab fa-facebook-square' />
                                                        </a>}
                                                        {user.instagram && <a class='mr-2' href={mu.asInstagramLink(user.instagram)} target='_blank' rel='noreferrer'>
                                                            <i class='fab fa-instagram' />
                                                        </a>}
                                                    </td>
                                                    <td>{user.cellphone}</td>
                                                    <td>{user.email}</td>
                                                </tr>
                                                {displayUser === user.id && <>
                                                    <tr>
                                                        <td colspan='4'>
                                                            <Teams stores={this.props.stores} teams={searchUserTeams} displayUser={displayUser} />
                                                        </td>
                                                    </tr>
                                                </>}
                                            </>);
                                        })}
                                    </tbody>
                                </table>
                            </div>}
                            {/* <xmp>{JSON.stringify(searchUsersResult, null, 4)}</xmp> */}
                        </div>
                    </div>
				</div>
			</>
		);
	}
}

export default Veterinary;
